const MEMORY_SIZE = 4096;
const NUM_REGISTERS = 16;

class Chip8 {
    constructor(monitor) {
        this.memory = new Uint8Array(MEMORY_SIZE); // 4KB of memory
        this.v = new Uint8Array(NUM_REGISTERS); // 16 8-bit registers

        this.index = 0;

        this.pc = 0x200; // Program counter starts at 0x200

        this.stack = [];

        this.sp = 0; // Stack pointer

        this.delayTimer = 0;

        this.soundTimer = 0;

        // keyboard

        //

        this.monitor = monitor;

        this.paused = false;
        this.speed = 10;


    }

    loadSpritesIntoMemory() {
        const sprites = [
            0xF0, 0x90, 0x90, 0x90, 0xF0, // 0
            0x20, 0x60, 0x20, 0x20, 0x70, // 1
            0xF0, 0x10, 0xF0, 0x80, 0xF0, // 2
            0xF0, 0x10, 0xF0, 0x10, 0xF0, // 3
            0x90, 0x90, 0xF0, 0x10, 0x10, // 4
            0xF0, 0x80, 0xF0, 0x10, 0xF0, // 5
            0xF0, 0x80, 0xF0, 0x90, 0xF0, // 6
            0xF0, 0x10, 0x20, 0x40, 0x40, // 7
            0xF0, 0x90, 0xF0, 0x90, 0xF0, // 8
            0xF0, 0x90, 0xF0, 0x10, 0xF0, // 9
            0xF0, 0x90, 0xF0, 0x90, 0x90, // A
            0xE0, 0x90, 0xE0, 0x90, 0xE0, // B
            0xF0, 0x80, 0x80, 0x80, 0xF0, // C
            0xE0, 0x90, 0x90, 0x90, 0xE0, // D
            0xF0, 0x80, 0xF0, 0x80, 0xF0, // E
            0xF0, 0x80, 0xF0, 0x80, 0x80  // F
        ];
        for (let i = 0; i < sprites.length; i++) {
            this.memory[i] = sprites[i];
        }
    }

    loadProgramIntoMemory(program) {
        for (let i = 0; i < program.length; i++) {
            this.memory[0x200 + i] = program[i];
        }
    }

    cycle() {
        for(let i = 0; i< this.speed; i++) {
            if (!this.paused) {
                let opcode = this.memory[this.pc] << 8 | this.memory[this.pc + 1];
                this.executeNextInstruction(opcode);
            }
        }
        if(!this.paused) {
            this.updateTimers();
        }
        // this.sound();
        this.monitor.paint();
    }

    updateTimers() {
        if (this.delayTimer > 0) {
            this.delayTimer -= 1;
        }
    
        if (this.soundTimer > 0) {
            this.soundTimer -= 1;
        }
    }    

    // each instruction is 2 bytes long
    interpretInstruction(instruction) {
        this.pc += 2; // increment program counter by 2 bytes
        const x = (instruction & 0x0F00) >> 8;
        const y = (instruction & 0x00F0) >> 4;

        // switch case for each instruction
        switch(instruction & 0xF000) {
            case 0x0000:
                switch(instruction){
                    case 0x00E0:
                        this.monitor.clear();
                        break;
                    case 0x00EE:
                        this.pc = this.stack.pop();
                }
                break;
            case 0x1000:
                this.pc = instruction & 0x0FFF; // jump to address NNN
                break;
            case 0x2000:
                this.stack.push(this.pc);
                this.pc = instruction & 0x0FFF; // call subroutine at NNN
                break;
            case 0x3000:
                // skip next instruction if Vx === NN
                if(this.v[x] === (instruction & 0xFF)) {
                    this.pc += 2;
                }
                break;
            case 0x4000:
                // skip next instruction if Vx !== NN
                if(this.v[x] !== (instruction & 0xFF)) {
                    this.pc += 2;
                }
                break;
            case 0x5000:
                // skip next instruction if Vx === Vy
                if(this.v[x] === this.v[y]) {
                    this.pc += 2;
                }
                break;
            case 0x6000:
                // LOAD Vx with NN
                this.v[x] = instruction & 0xFF;
                break;
            case 0x7000:
                // ADD NN to Vx
                this.v[x] += instruction & 0xFF;
                break;
            case 0x8000:
                switch (instruction & 0xF) {
                    case 0x0:
                        this.v[x] = this.v[y];
                        break;
                    case 0x1:
                        this.v[x] |= this.v[y];
                        break;
                    case 0x2:
                        this.v[x] &= this.v[y];
                        break;
                    case 0x3:
                        this.v[x] ^= this.v[y];
                        break;
                    case 0x4:
                        let sum = (this.v[x] += this.v[y]);
                        this.v[0xF] = 0;
                        if(sum > 0xFF){
                            this.v[0xF] = 1; // set v15 to 1 if sum > 255
                        }
                        this.v[x] = sum;
                        break;
                    case 0x5:
                        this.v[0xF] = 0;
                        if(this.v[x] > this.v[y]){
                            this.v[0xF] = 1;
                        }
                        this.v[x] -= this.v[y];
                        break;
                    case 0x6:
                        // SHR Vx, Vy
                        this.v[0xF] = this.v[x] & 0x1;
                        this.v[x] >>= 1;
                        break;
                    case 0x7:
                        // SUBN Vx, Vy
                        this.v[0xF] = 0;
                        if(this.v[y] > this.v[x]){
                            this.v[0xF] = 1;
                        }
                        this.v[x] = this.v[y] - this.v[x];
                        break;
                    case 0xE:
                        // SHL Vx, Vy
                        this.v[0xF] = this.v[x] >> 7;
                        this.v[x] <<= 1;
                        break;
                    default:
                        console.error(`Unknown instruction: ${instruction.toString(16)}`);
                }
                break;
            case 0x9000:
                // SNE Vx, Vy
                if(this.v[x] !== this.v[y]){
                    this.pc += 2;
                }
                break;
            case 0xA000:
                // LOAD I with NNN
                this.index = instruction & 0xFFF;
                break;
            case 0xB000:
                // JUMP to NNN + V0
                this.pc = (instruction & 0xFFF) + this.v[0];
                break;
            case 0xC000:
                let rand = Math.floor(Math.random() * 0xFF);
                this.v[x] = rand & (instruction & 0xFF);
                break;
            case 0xD000:
                let width = 8;
                let height = instruction & 0xF;
                this.v[0xF] = 0;
                for (let row = 0; row < height; row++) {
                    let sprite = this.memory[this.index + row];
                    for (let col = 0; col < width; col++) {
                        if ((sprite & 0x80) > 0) {
                            if (this.monitor.setPixel(this.v[x] + col, this.v[y] + row)) {
                                this.v[0xF] = 1;
                            }
                        }
                        sprite <<= 1;
                    }
                }
                break;
            case 0xE000:
                switch(instruction & 0xFF) {
                    case 0x9E:
                        // SKP Vx
                        if(this.keyboard.isKeyPressed(this.v[x])){
                            this.pc += 2;
                        }
                        break;
                    case 0xA1:
                        // SKNP Vx
                        if(!this.keyboard.isKeyPressed(this.v[x])){
                            this.pc += 2;
                        }
                        break;
                    default:
                        console.error(`Unknown instruction: ${instruction.toString(16)}`);
                }
                break;
            case 0xF000:
                switch(instruction & 0xFF) {
                    case 0x07:
                        // LOAD Vx with delay timer
                        this.v[x] = this.delayTimer;
                        break;
                    case 0x0A:
                        // WAIT for key press and store in Vx
                        this.paused = true;
                        this.keyboard.onNextKeyPress = function(key) {
                            this.v[x] = key;
                            this.paused = false;
                        }.bind(this);
                        break;
                    case 0x15:
                        // LOAD delay timer with Vx
                        this.delayTimer = this.v[x];
                        break;
                    case 0x18:
                        // LOAD sound timer with Vx
                        this.soundTimer = this.v[x];
                        break;
                    case 0x1E:
                        // ADD Vx to I
                        this.index += this.v[x];
                        break;
                    case 0x29:
                        // LOAD I with location of sprite for digit Vx
                        this.index = this.v[x] * 5;
                        break;
                    case 0x33:
                        // STORE BCD of Vx in I, I+1, I+2
                        this.memory[this.index] = parseInt(this.v[x] / 100);
                        this.memory[this.index + 1] = parseInt((this.v[x] % 100) / 10);
                        this.memory[this.index + 2] = parseInt(this.v[x] % 10);
                        break;
                    case 0x55:
                        // STORE V0 to Vx in memory starting at I
                        for (let i = 0; i <= x; i++) {
                            this.memory[this.index + i] = this.v[i];
                        }
                        break;
                    case 0x65:
                        // LOAD V0 to Vx from memory starting at I
                        for (let i = 0; i <= x; i++) {
                            this.v[i] = this.memory[this.index + i];
                        }
                        break;
                    default:
                        console.error(`Unknown instruction: ${instruction.toString(16)}`);
                }
        }
    }
}

module.exports = Chip8;